<template>
    <div style='position:relative;'>
        <div class="col-12" style='padding-left:0'>
            <form id='form' enctype="multipart/form-data" @submit.prevent="handleSubmit">        
                <div class="card">
                    <div class="card-header">
                        <strong>External Subsititude Teacher</strong>
                    </div>    
                    <div class="card-body" style='padding-left:0;padding-right:0'>       
                        <div class="col-12 flex">
                            <div class="col-5" >Teacher on Leave</div>
                            <div class="col-7">
                                <input type="text" class="form-control" placeholder="Search Teacher" v-model="o_teacher_id" @keyup="search_user(0)"/>
                            </div>
                        </div>
                        <div class="col-12 flex">
                            <div class="col-5" >External Teacher</div>
                            <div class="col-7">
                                <input type="text" class="form-control" placeholder="Search Teacher" v-model="n_teacher_id" @keyup="search_user(1)"/>
                            </div>
                        </div>
                        <div class="col-12 flex">
                            <div class="col-5" >From</div>
                            <div class="col-7">
                                <input type="date" class="form-control" v-model="from"/>
                            </div>
                        </div>
                        <div class="col-12 flex">
                            <div class="col-5" >To</div>
                            <div class="col-7">
                                <input type="date" class="form-control" v-model="to"/>
                            </div>
                        </div>
                    </div>

                    <div class="card-footer">
                        <button type="submit" class="btn btn-primary btn-sm">
                            <i class="fa fa-paper-plane"></i> Save
                        </button>
                        <button type="reset" class="btn btn-danger btn-sm">
                            <i class="fa fa-ban"></i> Cancel
                        </button>
                    </div>
                </div>    
            </form>   
        </div> 
        <div class="col-xs-3 col-md-3" style='position:fixed;right:25%;top:25%;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="recep_data"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_record"
                >
            <template v-slot:items="props">
                <tr v-on:click="select_user(props.item.username)" style='cursor:pointer'>
                    <td>Select</td>
                    <td>{{ props.item.name }}</td>
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div> 
    </div>   
</template>
<script>
export default {
    data(){
        return{
            hv_record:false,
            headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'name',sortable: false},
            ],
            key:'',
            curr_field:'', 
            recep_data:[],
            o_teacher_id:'',
            n_teacher_id:'',
            from:'',
            to:'',
        }
    },   
    async created(){
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
            today = yyyy + '-' + mm + '-' + dd;
        this.from = today;
    },
    async mounted(){
        
    },
    methods:{
        async search_user(n){
            if(n==0){
                this.key = this.o_teacher_id;
                this.curr_field = 'o';
            } else {
                this.key = this.n_teacher_id;
                this.curr_field = 'n';
            }
            var formData = new FormData();
            formData.append('key',this.key);
            await axios.post('subsitude/sub/backend/search_user.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.recep_data = response.data;
                            this.hv_record = true;
                        } else {
                            this.hv_record = false;
                        }
                    }
                )
        },
        async select_user(username){
            if(this.curr_field=='o'){
                this.o_teacher_id = username;
            } else {
                this.n_teacher_id = username;
            }
            this.key = '';
            this.hv_record = false;
        },
        handleSubmit(){
            var formData = new FormData();
            formData.append('leave_id',this.o_teacher_id);
            formData.append('ext',this.n_teacher_id);
            formData.append('from',this.from);
            formData.append('to',this.to);
            axios.post('subsitude/admin/backend/add_ext.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.$alert('Successfully Submitted record');
                            this.$router.push('sup_op');
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
td{
    transition: all 0.3s ease-in-out;
}
th{
    font-size:0.95rem;
}
.inner_overlay{
    z-index: 5;
    width:65%;
    max-height:60%;
    position:absolute;
    margin-left:auto;
    margin-top:auto;
    margin-bottom:auto;
    margin-right:auto;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color:rgba(255, 255, 255, 0.95);
    box-shadow: 5px 5px 5px 5px rgba(211, 211, 211, 0.6);
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
.sub_tr{
    height:5rem!important;
}
.flex{
    margin-top:1rem;
}
</style>




